@import "utils";
@import "variables";
@import "fonts";
@import "normalize";
@import "datepicker";
// @import "jqtree";
@import "transitions";

:first-child, .section:first-child {
  margin-top: 0;
}

:last-child, .section:last-child {
  margin-bottom: 0;
}

html, .base {
  color: $text-color;
}

html, .text {
  font-family: Arial, sans-serif;
  font-size: 1rem;
  font-style: normal;
  font-weight: normal;
  letter-spacing: normal;
  line-height: 1.375;
}

html {
  font-size: globalPerc($base);
}

body, .page {
  padding-left: $page-margin;
  padding-right: $page-margin;
  margin-top: -1.3em;
}

body {
  background-color: #fff;
  height: 100%;
  min-height: 100%;
  -webkit-overflow-scrolling: touch; /* to keep mobile Safari chrome */
  overflow-x: hidden;
  padding-bottom: 3rem + $page-margin;
  padding-top: $page-margin;
}


body:after {
  content: "0em";
  display: block;
  height: 0;
  position: absolute;
  overflow: hidden;
  width: 0;
}

h1, h2, h3, h4, h5, h6, .title, .title--sub, .title--subsub {
  color: #222;
  font-weight: bold;
}

h1, .title {
  font-size: 3em;
  line-height: 1;
  margin-bottom: .65em;
  margin-top: .65em;
}

h2, .title--sub, .onetomany-relation-content h1 {
  font-size: 2em;
  line-height: 1.1;
  margin-bottom: 1em;
  margin-top: 2em;
}

h3, h4, .title--subsub {
  font-size: 1rem;
  line-height: inherit;
}

p, ol, ul, blockquote, img, table, form, fieldset, pre, iframe, object, embed, video, audio, figure, figcaption, canvas, .p, h3, h4, .ui-dialog-buttonset {
  margin-top: $paragraph-margin;
}

p, ol, ul, blockquote, img, table, form, fieldset, pre, iframe, object, embed, video, audio, figure, figcaption, canvas, .p {
  margin-bottom: $paragraph-margin;
}

ol, ul {
  padding-left: 2em;
}

li {
  margin-bottom: $paragraph-margin/2;
  margin-top: $paragraph-margin/2;
}

blockquote {
}

blockquote::before {
}

blockquote::after {
}

a, a:before a:after, a::before, a::after, button, button:before, button:after, button::before, button::after, datalist, input, select, textarea, label, .trans {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  transition: .1s;
}

a {
  color: $primary-color;
  text-decoration: underline;
}

a:visited {
  color: lighten($primary-color, 20%);
}

a:hover, a:active {
  background-color: $primary-color;
  color: #fff;
  text-decoration: none;
  text-shadow: none;
}

a:active, button:active, input:active + label, input + label:active {
  box-shadow: inset 0 0 1em rgba(0, 0, 0, .1);
}

button, datalist, input, select, textarea, legend, label, .label, .input, .js .js-placeholder .form-label {
  line-height: normal;
}

button, datalist, input, select, textarea, .input, .js .js-placeholder .form-label {
  border-radius: 0;
  padding: .75em 1em;
  text-transform: none;
}

datalist, input, select, textarea, .input, .sp-replacer {
  background-color: #fff;
  border: 1px solid #dadada;
  color: $text-color;
}

input[type="file"] {
  border-width: 0;
}

select { /*height: 37px;*/
  -webkit-appearance: none;
  padding-right: 2em;
}

select::after {
  position: absolute;
  right: .5em;
  top: 50%;
  transform: translateY(-50%);
}

button {
  background-color: $primary-color;
  color: #fff;
  font-weight: bold;
  text-align: center;
}

button:hover, button:active {
  background-color: lighten($primary-color, 25%);
}

datalist, input, select, textarea {
  background-color: #fff;
  color: $text-color;
}

textarea {
  min-height: 8em;
}

p, ul, ol, li, blockquote, img, table, form, fieldset, pre, iframe, object, embed, video, audio, figure, canvas, .paragraph, h3, .ui-dialog-buttonset {
  margin-top: 1.375em;
}

p, ul, ol, li, blockquote, img, table, form, fieldset, pre, iframe, object, embed, video, audio, figure, canvas, .paragraph {
  margin-bottom: 1.375em;
}

button, datalist, input, select, textarea, legend, label, .label, .form-label, .form-field {
  line-height: normal;
}

