  background-position: center;
  background-size: 100%;
  content: "";
  display: block;
  height: 16px;
  left: 50%;
  margin: -8px 0 0 -8px;
  position: absolute;
  top: 50%;
  width: 16px;
}

.menu-nav {
  margin: 0;
  opacity: 1;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 102;
}

.js .menu-nav {
  position: fixed;
}

.menu-nav-button {
  display: inline-block;
  overflow: hidden;
  padding: 1.143em;
  text-decoration: none;
  vertical-align: top;
}

.menu-nav-button:focus, .menu-nav-button:hover, .menu-nav-button:active, .breadcrumbs-item:nth-last-child(2) .breadcrumbs-link:focus, .breadcrumbs-item:nth-last-child(2) .breadcrumbs-link:hover, .breadcrumbs-item:nth-last-child(2) .breadcrumbs-link:active {
  background-color: rgba(255, 255, 255, .25);
  color: #fff;
}

.menu-nav-button::before {
  background-size: 100%;
  background-image: url("menu.png", true);
  background-position: center;
  background-repeat: no-repeat;
  content: "";
  display: block;
  height: 15px;
  overflow: hidden;
  width: 21px;
}

.content-wrapper {
  display: table;
  position: relative;
  table-layout: fixed;
  width: 100%;
}

.main-content {
  display: table;
  padding-bottom: 6em;
  table-layout: fixed;
  width: 100%;
}

.content-body {
  position: relative;
}

.content-body, .form-group, .alert, .system-account, .controls-content, .table-header, .table-cell {
  padding-right: 1.5em;
}

.content-body, .form-group, .alert, .system-account, .controls-content, .table-header:first-child, .table-cell:first-child {
  padding-left: 1.5em;
}

.data-wrapper {
  overflow: hidden;
}

.controls {
  bottom: 0;
  min-height: 3.571em;
  left: 0;
  overflow: hidden;
  position: fixed;
  right: 0;
}

.control-list {
  margin: 0;
}

.control-item {
  display: inline-block;
  vertical-align: top;
  margin: 0 .5em 0 0;
}

.control-item:last-child {
  margin-right: 0;
}

.control-item-button {
  margin: -.65em 0;
}

.tricon {
  background-color: transparent;
  border-radius: 4px;
  cursor: move;
  display: inline-block;
  margin: -7px;
  padding: 7px;
  vertical-align: top;
}

.tricon:focus, .tricon:hover, .tricon:active {
  background-color: rgba(0, 0, 0, .125);
}

.tricon::before {
  background-image: url("tricon.png", true);
  content: "";
  display: inline-block;
  height: 18px;
  opacity: .25;
  vertical-align: top;
