}

.note {
  color: #838383;
  font-size: .875em;
}

.note, .note:first-child, .note:last-child {
  margin: .75em 0 0;
}

.btn {
  border: 1px solid transparent;
  display: inline-block;
  font-weight: bold;
  line-height: normal;
  padding: .65em .75em;
  text-align: center;
  text-decoration: none;
  vertical-align: top;
}

.btn:focus, .btn:hover, .btn:active {
}

.btn[disabled], .btn[disabled]:focus, .btn[disabled]:hover, .btn[disabled]:active {
  background-color: #aaa;
  border-color: #aaa;
  box-shadow: none;
  color: #d6d6d6;
}

.btn-default {
  background-color: $primary-color;
  color: #fff;
}

.btn-default:visited, .btn-preview:visited {
  color: #fff;
  color: rgba(255, 255, 255, .7);
}

.btn-default:focus, .btn-default:hover, .btn-default:active {
  background-color: lighten($primary-color, 30%);
  border-color: lighten($primary-color, 30%);
}

.btn-light {
  background-color: lighten($primary-color, 40%);
  border-color: lighten($primary-color, 40%);
  color: #fff !important;
}

.btn-alt {
  background-color: #fff;
}

.btn-quiet {
  background-color: $tertiary-color;
  color: #000;
  border-color: $tertiary-color;
}

.btn-quiet:focus, .btn-quiet:hover, .btn-quiet:active, .btn-light:focus, .btn-light:hover, .btn-light:active {
  background-color: #000 !important;
  color: #fff !important;
  border-color: #000;
}

.btn-cancel {
  background-color: transparent;
  border-color: transparent;
  text-decoration: underline;
}

.btn-cancel:focus, .btn-cancel:hover, .btn-cancel:active {
  background-color: $tertiary-color;
  border-color: $tertiary-color;
  color: #000;
  text-decoration: none;
}

.btn-tricon {
  background-image: url("tricon.png", true);
  background-repeat: no-repeat;
  background-position: 1.15em center;
  padding-left: 3.9em;
}

.btn-remove {
  background-color: #900;
  border-color: transparent;
  color: #fff;
}

.btn-remove:focus, .btn-remove:hover, .btn-remove:active {
  background-color: #700000;
}

.btn-remove-alt {
  opacity: 0;
}

.media-preview:hover .btn-remove-alt {
  opacity: 1;
}

.btn-preview {
  background-clip: padding-box;
  background-color: rgba(0, 0, 0, .45);
  border-color: rgba(0, 0, 0, .45);
  color: #fff;
}

.btn-preview:focus, .btn-preview:hover, .btn-preview:active {
  background-color: rgba(0, 0, 0, .55);
  border-color: rgba(0, 0, 0, .55);
}

.form-group, .alert, .header-top, .controls-content {
  padding-bottom: 2.5em;
  padding-top: 1.1em;
}

.form-group, .alert {
  margin-left: -1.5em;
  margin-right: -1.5em;
}

.form-group {
  background-color: #f2f2f2;
  display: block;
  margin-bottom: 1px;
  margin-top: 1px;
  position: relative;
}

.form-group-options {
  background-color: transparent;
  padding-bottom: 0;
  padding-top: 0;
  margin-bottom: $paragraph-margin*2;
  margin-top: $paragraph-margin*2;
}

.form-group-highlight {
  bottom: 0;
  left: 0;
