  margin-bottom: 1.1em;
}

.form-options {
  margin-left: -1.5em;
  margin-right: -1.5em;
}

.form-options-content {
  margin: -1px -.5px;
}

.form-option {
  display: inline-block;
  margin: 1px 0;
  position: relative;
  vertical-align: top;
  width: 100%;
}

@include respond-min-width(45em) {
  .form-option-grid-2, .form-option-grid-3 {
    width: 50%;
  }
}

@include respond-min-width(55em) {
  .form-option-grid-3 {
    width: 33.333%;
  }
}

@include respond-min-width(60em) {
  .form-option-grid-3 {
    width: 50%;
  }
}

@include respond-min-width(75em) {
  .form-option-grid-3 {
    width: 33.333%;
  }
}

.form-option-content {
  margin: 0 .5px;
}

.form-option-label {
  background-color: #e3e3e3;
  float: none !important;
  padding: 1.1em 1.5em 1.1em 3em;
}

.form-option-field {
  float: none;
  left: 1.5em;
  margin: 0 !important;
  position: absolute;
  top: 1.15em;
}

.form-control {
  display: block;
  margin: -.825em 0;
}

.form-field-short, .form-field-medium {
  min-width: 11em;
  width: 50%;
}

.form-field-content, .form-single-checkbox {
  position: relative;
  z-index: 2;
}

.form-single-checkbox {
  float: none !important;
}

.form-label-checkbox {
  float: left;
  margin: 0;
}

.form-field-checkbox {
  float: right;
}

.form-single-checkbox:not(:checked), .form-single-checkbox:checked { /* :checked here acting as a filter for older browsers */
  position: absolute;
  opacity: 0;
}

.form-single-checkbox.switch + .switch {
  background-color: #fff;
  border: 1px solid #dadada;
  border-radius: 9999em;
  display: block;
  font-size: 1.8em;
  height: 1.05em;
  margin: -.25em 0;
  overflow: hidden;
  transition-duration: .4s;
  transition-property: padding, width;
  width: 1.8em;
}

.form-single-checkbox.switch:checked + .switch {
  background-color: #529948;
  border-color: #529948;
  padding-left: .8em;
}

.form-single-checkbox.switch + .switch::before {
  background: #fff;
  border: 1px solid #dadada;
  border-radius: inherit;
  box-shadow: 0 0 .5em rgba(0, 0, 0, .125);
  content: "";
  display: block;
  height: 1.05em;
  margin: -1px;
  width: 1em;
}

.form-single-checkbox.switch:active + .switch::before {
  box-shadow: inset 0 0 .25em rgba(0, 0, 0, .2);
}

/*.form-single-checkbox.switch:focus + .switch { box-shadow: 0 0 .3em 1px red, 0 1px 1px hsla(0, 0%, 100%, .8); }*/
label.form-group:hover, .form-field-content:focus + .form-group-highlight, .single-checkbox:focus + .form-group-highlight, .form-single-checkbox:checked + .form-group-highlight, .form-option-label:focus, .form-option-label:hover, .form-option-label:active, .form-option-field:hover + .form-option-label, .form-option-field:focus + .form-option-label, .form-option-field:checked + .form-option-label {
