/*.form-single-checkbox:checked + .form-group-highlight, */
.form-option-field:checked:focus + .form-option-label, .form-option-field:checked:hover + .form-option-label, .form-option-field:checked:active + .form-option-label, .form-option-field:checked + .form-option-label:hover, .form-option-field:checked + .form-option-label:active {
  background-color: #c4c4c4;
}

