}

.login {
  background-color: #ebebeb;
}

.login-header-area {
  background-color: $primary-color;
  padding: 2em 0;
}

.login-header {
  text-align: center;
  color: #fff;
  margin: 0;
  text-decoration: underline;
  text-transform: uppercase;
}

.login-header:after {
  content: "";
  background-image: url("content-icon.png", true);
  background-repeat: no-repeat;
  background-position: right center;
  width: 32px;
  height: 65px;
  display: inline-block;
  vertical-align: -0.375em;
  margin-left: 0.15em;
}

.login-area {
}

.login-area-form {
  max-width: 22em;
  margin: 0 auto;
  padding: 2.222em 0;
  font-size: 18px;
}

.login-area-form .separate {
