}

.table-control {
  position: relative;
  padding: 0 !important;
  width: 1px;
}

.table-control-content {
  height: 100%;
  left: 100%;
  margin: 0;
  position: absolute;
  top: 0;
  width: 4.071em;
}

.table-control-list {
  display: none;
  margin: 0; /*position: absolute; top: .25em; bottom: 0; left: -25%; right: 0;*/
