$time-fast: .2s; $time: .4s; $time-slow: .6s;

.js .js-transform {
	-webkit-transition: -webkit-transform $time-fast ease-in-out;
	-moz-transition: -moz-transform $time-fast ease-in-out;
	-ms-transition: -ms-transform $time-fast ease-in-out;
	-o-transition: -o-transform $time-fast ease-in-out;
	transition: transform $time-fast ease-in-out;
}